import request from '@/utils/request'

// 
export function pullImage(name) {
  return request.scanerAddImage({
    url: '/images_pull',
    method: 'get',
    params: {
      name
    }
  })
}

// 
export function deleteImage(repotags) {
  return request.scanerAddImage({
    url: '/delete',
    method: 'delete',
    data: {
      repotags
    }
  })
}

// 
export function reloadPullImage(name) {
  return request.scanerAddImage({
    url: '',
    method: 'get',
    params: {
      name
    }
  })
}

// 
export function getProgress(name) {
  return request.scanerAddImage({
    url: '/progress',
    method: 'get',
    params: {
      name
    }
  })
}



